import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import '../components/Carousel.css'

const Carousel = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };

  return (
    <div className="carousel-container">
      <Slider {...settings}>
        <div>
          <img src='/team-01.png' alt="Joshua Kurniawan" />
          <div className="slide-text">Team Leader</div>
        </div>
        <div>
          <img src='/team-02.jpg' alt="Fajar Chresty" />
          <div className="slide-text">Team Member</div>
        </div>
        <div>
          <img src='/team-03.png' alt="David Lin" />
          <div className="slide-text">Team Member</div>
        </div>
        <div>
          <img src='team-04.png'alt="Karen Yapranata" />
          <div className="slide-text">Team Member</div>
        </div>
      </Slider>
    </div>
  );
};

export default Carousel;